.movie {
  display: flex;
  justify-content: flex-start;
  padding: 15px 30px;
  gap: 22px;
  border-bottom: 1px rgba(255, 255, 255, 0.062) solid;
  cursor: pointer;
  transition: background 300ms;
}
.movie:hover {
  background-color: #343a40;
}
.movie img {
  width: 40px;
}
.movie div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5px 0;
}
