.resume {
  background-color: #343a40;
  width: 100%;
  height: 100px;
  padding: 15px 25px;
  border-radius: 15px 0 0 15px;
  position: relative;
  transition: 300ms;
}
.resume p {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  font-size: 14px;
}
.resume button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #212529;
  color: white;
  font-size: 24px;
  border: none;
  align-self: center;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 15px;
}
