.movie {
  display: flex;
  justify-content: flex-start;
  padding: 15px 30px;
  gap: 22px;
  border-bottom: 1px rgba(255, 255, 255, 0.062) solid;
}
.movie img {
  width: 40px;
}
.movie div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5px 0;
  flex-grow: 1;
}
.movie div p:nth-child(2) {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  font-weight: 200;
}
.movie button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: red;
  border: none;
  align-self: center;
  cursor: pointer;
}
