.navbar {
  background-color: #6741d9;
  width: 95%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  margin-top: 20px;
  padding: 13px 35px;
  border-radius: 10px;
}
.navbar input {
  background-color: rgba(255, 255, 255, 0.185);
  font-size: 16px;
  padding: 15px 0;
  padding-left: 20px;
  width: 35%;
  border: none;
  border-radius: 8px;
  transition: 300ms;
}
.navbar input:focus {
  outline: none;
  transform: translateY(-3px);
}
.navbar input:focus::placeholder {
  color: transparent;
}
.navbar input::placeholder {
  color: rgba(255, 255, 255, 0.411);
}
