.overview {
  position: relative;
  display: flex;
  flex-direction: column;
}
.overview :global(.header) {
  display: flex;
  background-color: #343a40;
}
.overview :global(.about) {
  padding: 30px;
}

.overview :global(.title) {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}
.overview :global(.genre),
:global(.released),
:global(.imdb-rating) {
  font-weight: 300;
  font-size: 12px;
  margin-bottom: 10px;
}
.overview :global(.imdb-rating)::first-letter {
  font-size: 16px;
}
.overview img {
  width: 150px;
}
.overview > :global(.btn-back) {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: white;
  color: black;
  font-size: 24px;
  border: none;
  align-self: center;
  cursor: pointer;
  position: absolute;
  top: 7px;
  left: 7px;
}
.overview :global(.description) {
  width: 80%;
  align-self: center;
  margin-bottom: 30px;
}
.rate {
  background-color: #343a40;
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 130px;
  border-radius: 17px;
  justify-content: space-around;
}
.rate :global(.btn-add) {
  padding: 10px 50px;
  border: none;
  background-color: #6741d9;
  border-radius: 17px;
  font-weight: bold;
  font-size: 16px;
  color: white;
  cursor: pointer;
  transition: 300ms;
}
.rate :global(.btn-add):hover {
  background-color: #6f47e7;
}

.overview :global(.plot),
:global(.actors),
:global(.director) {
  margin-top: 20px;
  font-size: 12px;
  margin-bottom: 20px;
  line-height: 1.5;
}
